import React, { useState, useEffect } from "react";
import { requestRoninWalletConnector, ConnectorError } from "@sky-mavis/tanto-connect";
import "./styles.css";

function LoginRonin() {
  // Estados para conexión de Ronin Wallet
  const [connector, setConnector] = useState(null);
  const [connectedAddress, setConnectedAddress] = useState(null);
  const [walletError, setWalletError] = useState(null);
  
  // Estado para seleccionar el método de login: "" | "email" | "ronin"
  const [loginMethod, setLoginMethod] = useState("");

  // Estados para verificación por correo
  const [email, setEmail] = useState("");
  const [isCodeSent, setIsCodeSent] = useState(false);
  const [generatedCode, setGeneratedCode] = useState(""); // Código generado (simulación)
  const [inputCode, setInputCode] = useState("");
  const [emailError, setEmailError] = useState(null);
  const [isVerified, setIsVerified] = useState(false);

  // Bandera para redirigir solo una vez
  const [redirected, setRedirected] = useState(false);

  // Inicializa el conector para Ronin Wallet y recupera la cuenta guardada (si existe)
  useEffect(() => {
    const initConnector = async () => {
      try {
        const conn = await requestRoninWalletConnector();
        setConnector(conn);
      } catch (err) {
        if (err instanceof ConnectorError) {
          setWalletError(err.name);
        } else {
          setWalletError("Error initializing connector.");
        }
      }
    };
    initConnector();

    // Recupera la cuenta guardada en localStorage (si existe)
    const savedAccount = localStorage.getItem("connectedAccount");
    if (savedAccount) {
      setConnectedAddress(savedAccount);
    }
  }, []);

  // Redirige a game.html cuando ya se tiene la cuenta y no se ha redirigido aún
  useEffect(() => {
    if (connectedAddress && !redirected) {
      setRedirected(true);
      window.location.href = "./bin/game.html";
    }
  }, [connectedAddress, redirected]);

  // Función para enviar el código al correo (simulación)
  const handleSendCode = () => {
    // Validar correo simple
    if (!email || !/\S+@\S+\.\S+/.test(email)) {
      setEmailError("Por favor ingresa un correo válido.");
      return;
    }
    setEmailError(null);
    // Generar un código de 6 dígitos de forma aleatoria
    const code = Math.floor(100000 + Math.random() * 900000).toString();
    setGeneratedCode(code);
    setIsCodeSent(true);
    // En producción se enviaría el código desde un backend
    alert(`Código enviado (simulación): ${code}`);
  };

  // Función para verificar el código ingresado por el usuario
  const handleVerifyCode = () => {
    if (inputCode === generatedCode) {
      setIsVerified(true);
      alert("Código verificado exitosamente.");
      // Al verificar por correo, almacenamos el valor "0x000_Gmail" como cuenta conectada  
      setConnectedAddress("0x000_Gmail");
      localStorage.setItem("connectedAccount", "0x000_Gmail");
    } else {
      setEmailError("El código ingresado es incorrecto.");
    }
  };

  // Función para conectar mediante Ronin Wallet (extensión)
  const handleRoninLogin = async () => {
    if (!connector) return;
    try {
      const connectResult = await connector.connect();
      if (connectResult) {
        const account = connectResult.account;
        setConnectedAddress(account);
        localStorage.setItem("connectedAccount", account);
        setWalletError(null);
      }
    } catch (err) {
      console.error("Error connecting (extension):", err);
      setWalletError("Error connecting to Ronin Wallet.");
    }
  };

  // Función para desconectar y limpiar el localStorage
  const handleDisconnect = () => {
    setConnectedAddress(null);
    localStorage.removeItem("connectedAccount");
  };

  // Renderizado según el método de login seleccionado
  // Si aún no se eligió método, se muestran las dos opciones
  if (!loginMethod) {
    return (
      <div className="login-container">
        <h2>Seleccione el método de login</h2>
        <button className="button-custom" onClick={() => setLoginMethod("email")}>
          Iniciar sesión con Correo
        </button>
        <button className="button-custom" onClick={() => setLoginMethod("ronin")}>
          Iniciar sesión con Ronin Wallet
        </button>
      </div>
    );
  }

  // Si se seleccionó el método de correo y aún no se ha verificado
  if (loginMethod === "email" && !isVerified) {
    return (
      <div className="login-container">
        <h2>Login con Correo</h2>
        {emailError && <p className="error">{emailError}</p>}
        <input
          type="email"
          placeholder="Ingresa tu correo"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
        />
        {!isCodeSent ? (
          <button className="button-custom" onClick={handleSendCode}>
            Enviar Código
          </button>
        ) : (
          <>
            <input
              type="text"
              placeholder="Ingresa el código recibido"
              value={inputCode}
              onChange={(e) => setInputCode(e.target.value)}
            />
            <button className="button-custom" onClick={handleVerifyCode}>
              Verificar Código
            </button>
          </>
        )}
        <button className="button-custom" onClick={() => setLoginMethod("")}>
          Volver a elegir método
        </button>
      </div>
    );
  }

  // Si se seleccionó el método Ronin Wallet
  if (loginMethod === "ronin") {
    return (
      <div className="login-container">
        <h2>Login con Ronin Wallet</h2>
        {walletError && <p className="error">{walletError}</p>}
        <button className="button-custom" onClick={handleRoninLogin}>
          Conectar Ronin Wallet
        </button>
        <button className="button-custom" onClick={() => setLoginMethod("")}>
          Volver a elegir método
        </button>
      </div>
    );
  }

  // Si se ha verificado el correo o se ha conectado la wallet, se muestra la pantalla de cuenta conectada
  return (
    <div className="login-container">
      <h2>Cartera Conectada</h2>
      <p>Cartera conectada: {connectedAddress}</p>
      <button className="button-custom" onClick={handleDisconnect}>
        Desconectar cartera
      </button>
    </div>
  );
}

export default LoginRonin;
